.main-menu{
    position: relative;
}
.main-menu ul {
    list-style: outside none none;
    margin: 0;
    padding: 0;
    text-align: center;
    margin-right: -18px;
    @include breakpoint(xxl){
        margin-right: -14px;
    }
    
    li {
        display: inline-block;
        position: relative;
        &.has-megamenu {
            position: static;
        }
        a {
            color: var(--gray-700);
            display: block;
            font-size: var(--fs-lg-16);
            font-family: $body-font;
            @include rt-trs(.24s);
            padding: 0 18px;
            font-weight: 400;
            @include breakpoint(xxl){
                padding: 0 14px;
            }
            @include breakpoint(xl){
                padding: 0 5px;
               // font-size: 15px;
            }
            &:hover{
                text-decoration: none;
            }
        }
    }
    
}

.main-menu a:foucs {
    outline: none;
    text-decoration: none;
}

.main-menu>ul>li.current-menu-item>a,
.main-menu ul li a.active

{
    color: var(--primary-500);
}


/*change hover color*/
.main-menu>ul>li>a{
    line-height: 104px;
    @include breakpoint(lg){
        line-height: 1.9;
    }
}

.main-menu>ul>li:hover>a {
    color: var(--primary-500);
}


/*li dot hover change*/


/*men item carve icon*/

.main-menu>ul>li.menu-item-has-children>a:after {
    content: "\f101";
    margin-left: 8px;
    font-family: "flaticon";
    font-size: 12px;
    @include rt-trs(.24s);
    @include breakpoint(md) {
        margin-left: 0;
        position: absolute;
        right: 15px;
    }
}

.main-menu>ul>li.menu-item-has-children:hover>a:after {
    color: var(--primary-500);
}

.main-menu>ul>li.menu-item-has-children.open>a:after {
    content: '\eab9';
}


/*sub menu*/

.main-menu>ul>li.menu-item-has-children>ul.sub-menu {
    position: absolute;
    left: 0;
    top: 80%;
    background: #fff;
    z-index: 999;
    color: #fff;
    text-align: left;
    border-radius: 8px;
    min-width: 248px;
    transition: all 0.24s ease-in-out;
    padding: 12px;
    border: 1px solid var(--gray-100);
    box-shadow: 0px 12px 32px rgba(25, 31, 51, 0.08);
    @include hidden();
    // &:before {
    //     content: "";
    //     position: absolute;
    //     top: 2px;
    //     left: 45px;
    //     box-sizing: border-box;
    //     border: 7px solid #495057;
    //     border-color: transparent transparent #ffffff #ffffff;
    //     transform-origin: 0 0;
    //     transform: rotate(135deg);
    //     box-shadow: -2px 2px 2px -1px rgba(60, 72, 88, 0.15);
    //     @include breakpoint(md) {
    //         display: none
    //     }
    // }
}

.main-menu>ul>li.menu-item-has-children:hover>ul.sub-menu {
    @include vis();
    top: 74%;
}

.main-menu>ul>li.menu-item-has-children:hover>.rt-mega-menu {
    @include vis();
    top: 74%;
}

.main-menu>ul>li.menu-item-has-children>ul.sub-menu>li,
.main-menu ul ul li {
    display: block;
}

.main-menu>ul>li.menu-item-has-children>ul.sub-menu>li>a,
.main-menu ul ul li a {
    display: block;
    padding: 8px 15px;
    border-bottom: 1px solid rgba(#ccc, 0.0);
    text-transform: capitalize;
    color:var(--gray-700);
    font-weight: 400;
    @include afterparent();
    font-size: 14px;
    border-radius: 4px;
}

.main-menu>ul>li.menu-item-has-children>ul.sub-menu>li:last-child>a,
.main-menu ul ul li:last-child>a {
    border-bottom: 1px solid transparent !important;
}

.main-menu>ul>li.menu-item-has-children>ul.sub-menu>li:hover>a,
.main-menu ul ul>li:hover>a {
   // padding-left: 25px;
    color: var(--primary-500);

    background: var(--primary-50);
}


