.mobile-menu {
    display: none
}

.menu-click {
    margin-left: 15px;
    cursor: pointer;
    i {
        font-size: 23px;
        color: var(--gray-100);
    }
}

.menu-icon {
    cursor: pointer;
    margin-left: 15px;
    position: relative;
    height: 21px;
    span {
        display: block;
        background: var(--gray-900);
        height: 2px;
        width: 25px;
        margin-bottom: 5px;
        transition: all 0.24s ease-in-out;
        &:first-child {
            margin-top: 10px;
        }
        &:last-child {
            margin-bottom: 0px;
        }
        &:nth-child(2) {
            width: 15px;
        }
        &:nth-child(3) {
            width: 20px;
        }
    }
}

.mblnav-close {
    position: absolute;
    right: 30px;
    top: 30px;
    @include hw(35px);
    border-radius: 50%;
    text-align: center;
    background: var(--primary-500);
    cursor: pointer;
    color: #fff;
}

.mobile-menu {
    display: none;
}
.mblbrand-logo{
    display: none;
    padding-left: 15px;
    margin-bottom: 30px;
    @include breakpoint(lg){
        display: block;
    }
}

.rt-mobile-menu-overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0, 0, 0, 0.5);
    @include hidden();
    @include rt-trs(0.24s);
    &.active {
        z-index: 7;
        @include vis();
    }
}

.rt-mobile-menu-close {
    display: none;
    @extend .mblnav-close;
    @include hw(30px);
    right: 10px;
    font-size: 12px;
    @include breakpoint(lg) {
        display: block;
    }
}

//experintam mobile menu
// white menu css 
@media (min-width: 992px) {
    .white-menu {
        .main-menu>ul>li>a{
            color: var(--gray-400);
        }
        .main-menu>ul>li.current-menu-item>a {
            color: #fff;
        }
        .main-menu>ul>li:hover>a{
            color: #fff;
            &:after{
                color: #fff;
            }
        }
        
    }
    
}